@import ../_mixins
@import ../_var

.main-menu.container
  display: flex
  flex-direction: column
  header
    position: relative
    height: $header-low-height
    padding: 0 15px
    display: flex
    justify-content: space-between
    align-items: center
    border-bottom: $dark-border
  .left-tools, .right-tools
    display: flex
    justify-content: space-between
  .left-tools
    button.edit-btn
      width: 70px
  h2
    @include text-overflow
    margin: 0 30px !important
    flex: 1
    text-align: center
    &.logo
      flex: none
      padding: 0 10px
      background-image: linear-gradient(45deg, #9CA3AF, #6B7280, #9CA3AF)
      background-size: 200% 100%
      animation: logo-gradient 5s infinite
      background-clip: text
      font-family: sofia, sans-serif
      color: #0000
  main
    flex: 1
    overflow: hidden
    display: flex
    position: relative
    button.new-note
      position: fixed
      right: 20px
      bottom: calc(20px + $safe-bottom)

@keyframes logo-gradient
  from
    background-position-x: 0
  50%
    background-position-x: 100%
  to
    background-position-x: 0

.side-menu
  transition: all 0.5s
  width: 300px
  height: 100%
  display: flex
  flex-direction: column
  border-right: $default-border
  background-color: #eee3
  footer
    border-top: $default-border
    padding: 10px
    padding-bottom: calc(10px + $safe-bottom)
    overflow: hidden
    display: flex
    > * + *
      margin-left: 10px

.aside-drawer
  top: $header-low-height !important
  bottom: 0 !important
  height: unset !important
  .side-menu
    background: #0000

@media screen and (max-width: 640px)
  button.large, main > .side-menu
    display: none
  .left-tools, .right-tools
    width: 75px

@media screen and (min-width: 640px)
  button.small, .aside-btn, .aside-drawer
    display: none
  .left-tools, .right-tools
    width: 130px

.tag-circle
  width: 12px
  height: 12px
  border-radius: 6px

.tag-list
  flex: 1
  overflow: hidden auto
  padding: 20px
  .tag-wrapper
    overflow: hidden
    border-radius: 1em
    will-change: transform
    &:not(:last-of-type)
      margin-bottom: 10px

.tag-item
  height: 3em
  padding: 0 10px
  display: flex
  align-items: center
  cursor: pointer
  transition: height, background, padding
  transition-duration: 0.3s
  &.editing
    height: 8em
    padding: 0 20px
    flex-direction: column
    justify-content: space-evenly
    .buttons
      width: 100%
      display: flex
      justify-content: space-between
  &.curr
    background-color: #f1f0f5
  > .tag-circle
    margin: 0 12px 0 7px
  .all-note-icon
    margin: 0 10px 0 7px
  .tag-name
    @include text-overflow
    flex: 1
    margin: 0 10px 0 0 !important
  .tag-num
    margin-right: 10px

.ant-select-selection-item, .ant-select-item-option-content
  display: flex
  align-items: center

.note-list
  flex: 1
  padding-bottom: calc(80px + $safe-bottom)
  overflow: hidden auto
  position: relative
  -webkit-transform: translateZ(0)
  .head-tools
    height: 60px
    background-color: #fff
    display: flex
    align-items: center
    justify-content: center
    > * + *
      margin-left: 10px 
    &.editing
      position: sticky
      top: 0
      z-index: 100
      border-bottom: $light-border
    .search-input
      width: 200px
      height: 32px
      background-color: #eee8
      border-radius: 50px
      &:focus-within
        background-color: #eee
    button
      background-color: #eee8
      &:hover
        background-color: #eee

@keyframes note-item-in
  from
    height: 0
    opacity: 0
  to
    height: 5em
    opacity: 1

.note-item
  height: 5em
  padding: 0 1em
  margin: 0 20px
  display: flex
  align-items: center
  cursor: pointer
  position: relative
  border-bottom: $light-border
  &::before
    @include pseudo-el(0, -20px, -1px, -20px)
    transition: background 0.3s
  &.selected::before
    background-color: $blue-color-light
  &:not(.loaded)
    animation: note-item-in 0.3s
  .timg-wrapper
    width: 3em
    margin-right: 1em
    display: flex
    justify-content: center
    position: relative
    .timg
      max-width: 3em
      max-height: 3.5em
      border: $light-border
      background-color: white
    .cloud-icon
      position: absolute
      bottom: 0
      left: 0
      zoom: 0.7
  .content
    flex: 1
    padding: 5px
    overflow: hidden
    .name
      @include text-overflow
      margin: 1px 0 1px 6px
      font-size: 1.2em
    .name-input
      font-size: 1.2em
      padding: 0 0 0 5px
      background: #0000
      border-color: #eee
    .date
      @include text-overflow
      font-size: 0.8em
      margin: 0 0 0 6px
      color: #0006

.tag-select
  display: flex
  align-items: center
  max-width: 150px
  .tag-circle
    margin-right: 10px
  .none-tag-icon
    margin-right: 8px
  .name
    @include text-overflow
    flex: 1

.sort-drop ul
  margin: 0
